<template>
  <div class="container py-10 mx-auto" v-if="allChapters.length > 0">
    <div v-if="selected == 1">
      <div class="space-y-3 text-white">
        <div
          v-for="(element, i) in allChapters"
          :key="i"
          class="flex flex-col items-center justify-center  bg-gradient-to-r from-indigo-600 via-violet-700 to-purple-800"
        >
          <label class="inline-flex items-center gap-3 mt-3">
            <input
              type="checkbox"
              v-model="allChapters[i].is"
              class="w-5 h-5 text-red-600 form-checkbox"
            />
            {{ element.is ? "Selected" : "Excluded" }}
          </label>

          <h2 class="text-xl font-semibold text-center bangla">
            {{ element.chapter }}
          </h2>
          <h2 class="text-lg font-semibold">{{ element.subject }}</h2>
        </div>
      </div>
      <div class="mt-5 text-center">
        <button
          @click="goNext"
          class="px-6 py-3 font-semibold text-white transition duration-200 shadow  hover:shadow-lg bg-gradient-to-tl from-indigo-500 hover:from-blue-700 to-indigo-800"
        >
          Next
        </button>
      </div>
    </div>
    <div v-else-if="selected == 2" class="container mx-auto">
      <draggable
        v-model="allChapters"
        tag="div"
        class="space-y-5"
        item-key="id"
      >
        <template #item="{ element }">
          <div
            class="flex flex-col items-center justify-center p-3 space-y-3 text-white cursor-move  bg-gradient-to-r from-indigo-600 via-violet-700 to-purple-800"
          >
            <h2 class="text-xl font-semibold text-center bangla">
              {{ element.chapter }}
            </h2>
            <h2 class="text-lg font-semibold">{{ element.subject }}</h2>
          </div>
        </template>
      </draggable>
      <div class="mt-5 text-center">
        <button
          @click="goNext"
          class="px-6 py-3 font-semibold text-white transition duration-200 shadow  hover:shadow-lg bg-gradient-to-tl from-indigo-500 hover:from-blue-700 to-indigo-800"
        >
          Next
        </button>
      </div>
    </div>
    <div v-else-if="selected == 3" class="container mx-auto">
      <draggable v-model="routine" tag="div" class="space-y-5" item-key="id">
        <template #item="{ element }">
          <div
            class="flex flex-col items-center justify-center p-3 space-y-3 text-white cursor-move  bg-gradient-to-r from-indigo-600 via-violet-700 to-purple-800"
          >
            <h2 class="text-xl font-semibold text-center bangla">
              {{ element.chapter }}
            </h2>
            <h2 class="text-lg font-semibold">{{ element.subject }}</h2>
            <h2 class="text-lg font-semibold">{{ element.start }}</h2>
            <h2 class="text-lg font-semibold">{{ element.end }}</h2>
          </div>
        </template>
      </draggable>
    </div>
  </div>
  <div v-else class="h-screen text-center">
    <p class="mt-10 text-center">Please wait...</p>
  </div>
</template>

<script>
import _ from "lodash";
import draggable from "vuedraggable";
import { mapState } from "vuex";
import { routine } from "../plugins/routine";
export default {
  components: {
    draggable,
  },
  data() {
    return {
      routine: [],
      selected: 1,
      allChapters: [],
      totalDays: 366,
      targetDays: 70,
      roundDaysSum: 0,
    };
  },
  methods: {
    goNext() {
      this.selected += 1;
      this.allChapters = this.allChapters.filter((s) => s.is);
      this.prepareRoutine();
      window.scroll(0, 0);
    },
    getRoutine() {
      this.allChapters = routine.routines;
      this.prepareRoutine();
    },
    prepareRoutine() {
      let targetChapters = this.allChapters;
      let totalDays = parseInt(366);
      let targetDays = parseInt(this.options.days);

      let totalper = _.sumBy(targetChapters, (k) => (k.days / totalDays) * 100);
      let startDate = new Date(this.options.start).getTime();
      this.routine = this.allChapters.map((r, i) => {
        let percentage = (r.days / totalDays) * 100;
        let factor = targetDays / totalper;
        let cdays = r.is ? percentage * factor : 0;
        let endDate = new Date(startDate + 24 * cdays * 3600000);
        let chapter = {
          id: i,
          is: true,
          subject: r.subject,
          chapter: r.chapter,
          percentage,
          factor,
          days: cdays,
          start: new Date(startDate).toLocaleString(),
          end: endDate.toLocaleString(),
          roundDays: Math.ceil(cdays),
        };
        startDate = endDate.getTime();
        return chapter;
      });

      this.roundDaysSum = _.sumBy(this.routine, "roundDays");
    },
  },
  created() {
    this.getRoutine();
  },
  computed: {
    ...mapState(["options"]),
  },
};
</script>

